import { __ } from "../../utils";
import { crawl, findSitemap } from "@devowl-wp/sitemap-crawler";
async function crawlSitemap(setStep) {
  const textOfferSupport = __('If you think a sitemap exists but you get this error, please <a href="%s" target="_blank" />contact our support</a> and we will look individually where the problem is in your WordPress installation.', __("https://devowl.io/support/"));

  // Use original `home_url` to support WordPress installations within a subfolder (https://git.io/JRM9g)
  let rootUrl = window.realCookieBannerQueue.originalHomeUrl;
  rootUrl = rootUrl.split("?", 2)[0];
  setStep?.("find-sitemap");
  const sitemapUrl = await findSitemap(rootUrl, "rcb-force-sitemap", ["?sitemap=index&rcb-force-sitemap=1"]);
  if (sitemapUrl === false) {
    throw new Error(`${__("We didn't find a sitemap on your website. We need it to scan all the subpages of your website. Do you have this feature disabled in your WordPress?")} ${textOfferSupport}`);
  }
  setStep?.("collect-sitemap");
  let sites = [];
  try {
    sites = await crawl(rootUrl, sitemapUrl);
  } catch (e) {
    throw new Error(`${__("The sitemap could not be parsed. Therefore, we cannot check for services on your website.")} ${textOfferSupport}`);
  }
  if (sites.length === 0) {
    throw new Error(`${__("The sitemap is empty. So, we could not add any URLs to the scanner.")} ${textOfferSupport}`);
  }
  return sites;
}
export { crawlSitemap };